<template>
  <div>
    <ul>
      <li v-for="route in $router.options.routes">
        <router-link :to="{ path: route.path }">{{ route.name }}</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'Home',
  mounted () {
    console.log(this.$router.options.routes)
  }
}
</script>
<style scoped>
  li {
    list-style: none
  }
</style>



