<template>
  <div>
    <my-slot>
     <h1 slot="header">여기에 페이지 제목이 위치합니다</h1>
      <p>메인 컨텐츠의 단락입니다.</p>
      <p>하나 더 있습니다.</p>
      <p slot="footer">여기는 footer입니다.</p>
    </my-slot>
    <br><br><br>
    <my-slot>
      <p> 헤더가 없는 컴포넌트 입니다 </p>
      <p slot="footer">여기는 footer입니다.</p>
    </my-slot>

    <div style="display:flex;">
        <pre v-highlightjs='slotTemplate' style="flex-grow:1;">
            <code class="html" style='text-align:left'>
            </code>
        </pre>
        <pre v-highlightjs='slotJs' style="flex-grow:1; margin-left: 10px">
            <code class="javascript" style='text-align:left'>
            </code>
        </pre>
    </div>
  </div>
</template>
<script>
import mySlot from '../components/Slot'
export default {
  name: 'SlotView',
  components: {
    mySlot
  },
  data () {
    return {
      slotTemplate: `<template>
  <div>
    <my-slot>
     <h1 slot="header">여기에 페이지 제목이 위치합니다</h1>
      <p>메인 컨텐츠의 단락입니다.</p>
      <p>하나 더 있습니다.</p>
      <p slot="footer">여기는 footer입니다.</p>
    </my-slot>
    
    <my-slot>
      <p> 헤더가 없는 컴포넌트 입니다 </p>
      <p slot="footer">여기는 footer입니다.</p>
    </my-slot>
  </div>
</template>
      `,
      slotJs: `
import mySlot from '../components/Slot'
export default {
  name: 'SlotView',
  components: {
    mySlot
  }
}`
    }
  }
}
</script>




