<template>
  <div>
      <h1>{{ testData }}</h1>

      <br /><br /><br />

      <div style="display:flex;">
          <pre v-highlightjs='dataTemplate' style="flex-grow:1;">
              <code class="html" style='text-align:left'>
              </code>
          </pre>
          <pre v-highlightjs='dataScript' style="flex-grow:1; margin-left: 10px">
              <code class="javascript" style='text-align:left'>
              </code>
          </pre>
      </div>
  </div>
</template>
<script>
export default {
  name: 'data',
  data () {
    return {
      testData: 'testData',
      dataTemplate: `<template>
  <div>
      <h1>{{ testData }}</h1>
  </div>
</template>`,
      dataScript: `export default {
  name: 'data',
  data () {
    return {
      testData: 'testData'
    }
  },
  mounted () {
    setTimeout(() => {
      this.testData = 'testData changed'
    }, 3000)
  }
}`
    }
  },
  mounted () {
    setTimeout(() => {
      this.testData = 'testData changed'
    }, 3000)
  }
}
</script>





