<template>
  <div>
      <h1 v-on:click="clearMsg">{{ returnMsg }}</h1>
      <h1 v-on:click="clearDate">{{ returnDate }}</h1>

      <br /><br /><br />

      <div style="display:flex;">
          <pre v-highlightjs='computedTemplate' style="flex-grow:1;">
              <code class="html" style='text-align:left'>
              </code>
          </pre>
          <pre v-highlightjs='computedScript' style="flex-grow:1; margin-left: 10px">
              <code class="javascript" style='text-align:left'>
              </code>
          </pre>
      </div>
  </div>
</template>
<script>
export default {
  name: 'Computed',
  data () {
    return {
      msg: '',
      time: '',
      computedTemplate: `<template>
  <div>
      <h1 v-on:click="clearMsg">{{ returnMsg }}</h1>
      <h1 v-on:click="clearDate">{{ returnDate }}</h1>
  </div>
</template>`,
      computedScript: `export default {
  name: 'Computed',
  data () {
    return {
      msg: '',
      time: ''
    }
  },
  computed: {
    returnMsg () {
      // msg data에 의존적임
      // msg data가 바뀌면 해당 값을 참조하는 것을 업데이트
      const date = new Date()
      const HHMMSS = date.getHours() + '시' + date.getMinutes() + '분' + date.getSeconds() + '초'
      this.msg = HHMMSS
      return this.msg
    },
    returnDate () {
      // 의존하는 데이터가 없음
      // 한 번 계산되면 바뀌지 않음
      const date = new Date()
      const HHMMSS = date.getHours() + '시' + date.getMinutes() + '분' + date.getSeconds() + '초'
      return HHMMSS
    }
  },
  methods: {
    clearMsg: function () {
      this.msg = ''
    },
    clearDate: function () {
      this.time = ''
    }
  }
}`
    }
  },
  computed: {
    returnMsg () {
      // msg data에 의존적임
      // msg data가 바뀌면 해당 값을 참조하는 것을 업데이트
      const date = new Date()
      const HHMMSS = date.getHours() + '시' + date.getMinutes() + '분' + date.getSeconds() + '초'
      this.msg = HHMMSS
      return this.msg
    },
    returnDate () {
      // 의존하는 데이터가 없음
      // 한 번 계산되면 바뀌지 않음
      const date = new Date()
      const HHMMSS = date.getHours() + '시' + date.getMinutes() + '분' + date.getSeconds() + '초'
      return HHMMSS
    }
  },
  methods: {
    clearMsg: function () {
      this.msg = ''
    },
    clearDate: function () {
      this.time = ''
    }
  }
}
</script>




