import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home'
import Data from '../views/Data'
import Methods from '../views/Methods'
import Computed from '../views/Computed'
import Props from '../views/Props'
import Slot from '../views/Slot'
import MyKeepAlive from '../views/My-Keep-Alive'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/data',
      name: 'VueData',
      component: Data
    },
    {
      path: '/methods',
      name: 'VueMethods',
      component: Methods
    },
    {
      path: '/computed',
      name: 'VueComputed',
      component: Computed
    },
    {
      path: '/props',
      name: 'VueProps',
      component: Props
    },
    {
      path: '/slot',
      name: 'VueSLot',
      component: Slot
    },
    {
      path: '/keep-alive',
      name: 'VueKeepAlive',
      component: MyKeepAlive
    }
  ]
})



