<template>
  <div>
      <h1 v-on:click='clickHandler'>Event Handler</h1>
      <h1 v-on:click='clickHandler2'>Arrow Function Handler</h1>

      <br /><br /><br />

      <div style="display:flex;">
          <pre v-highlightjs='methodsTemplate' style="flex-grow:1;">
              <code class="html" style='text-align:left'>
              </code>
          </pre>
          <pre v-highlightjs='methodsScript' style="flex-grow:1; margin-left: 10px">
              <code class="javascript" style='text-align:left'>
              </code>
          </pre>
      </div>
  </div>
</template>
<script>
export default {
  name: 'Methods',
  data () {
    return {
      methodsTemplate: `<div>
      <h1 v-on:click='clickHandler'>Event Handler</h1>
      <h1 v-on:click='clickHandler2'>Arrow Function Handler</h1>
  </div>`,
      methodsScript: `export default {
  name: 'Methods',
  methods: {
    clickHandler: function () {
      console.log(this)
    },
    clickHandler2: () => {
      console.log(this)
    }
  }
}`
    }
  },
  methods: {
    clickHandler: function () {
      console.log(this)
    },
    clickHandler2: () => {
      console.log(this)
    }
  }
}
</script>




