<template>
  <div>
    <my-props msg='Hello Props'></my-props>
    <br /><br /><br />
    <my-props></my-props>
    <br /><br /><br />
    <my-props :msg='dynamicMsg'></my-props>
    <button @click='clickHandler'>Change Message</button>

    <br /><br /><br />
    
    <div style="display:flex;">
        <pre v-highlightjs='propsTemplate' style="flex-grow:1;">
            <code class="html" style='text-align:left'>
            </code>
        </pre>
        <pre v-highlightjs='propsJs' style="flex-grow:1; margin-left: 10px">
            <code class="javascript" style='text-align:left'>
            </code>
        </pre>
    </div>
  </div>
</template>
<script>
import myProps from '../components/Props'
export default {
  name: 'Props',
  data () {
    return {
      count: 0,
      dynamicMsg: 0,
      propsTemplate: `// Props.vue
<template>
    <div>
        <my-props msg='Hello Props'></my-props>

        <my-props></my-props>

        <my-props :msg='dynamicMsg'></my-props>
        <button @click='clickHandler'>Change Message</button>
    </div>
</template>`,
      propsJs: `// script
import myProps from '../components/Props'
export default {
    name: 'Props',
    data () {
        return {
        count: 0,
        dynamicMsg: 0
        }
    },
    methods: {
        clickHandler: function () {
            this.dynamicMsg = (++this.count) + ''
        }
    },
    components: {
        myProps
    }
}`
    }
  },
  methods: {
    clickHandler: function () {
      this.dynamicMsg = (++this.count)
    }
  },
  components: {
    myProps
  }
}
</script>


