<template>
  <div class="container">
    <header class="header">
        <slot name="header"></slot>
    </header>
    <main class="main">
        <slot></slot>
    </main>
    <footer class="footer">
        <slot name="footer"></slot>
    </footer>
    </div>
</template>
<script>
export default {
  name: 'Slot'
}
</script>
<style>
    .container {
        background-color:#00aaaa;
        color:wheat;
    }
    .header {
        background-color:#008b8b
    }
    .main {
        background-color: #005d5d
    }

    .footer {
        background-color: #002f2f
    }
</style>





