<template>
    <div>
        <button @click="selectedComponent = 'MyKeepAliveCmp'">KeepAliveComponent</button>
        <button @click="selectedComponent = 'Props'">PropsComponent</button>
        <br /><br /><br />
        <div>Keep-Alive Component</div>
        <keep-alive>
          <component :is="selectedComponent"></component>
        </keep-alive>
        <br /><br /><br />
        <div>Normal Component</div>
        <component :is="selectedComponent"></component>

        <br /><br /><br />
    
        <div style="display:flex;">
            <pre v-highlightjs='keepAliveViewTemplate' style="flex-grow:1;">
                <code class="html" style='text-align:left'>
                </code>
            </pre>
            <pre v-highlightjs='keepAliveViewJs' style="flex-grow:1; margin-left: 10px">
                <code class="javascript" style='text-align:left'>
                </code>
            </pre>
        </div>
        <div style="display:flex;">
            <pre v-highlightjs='keepAliveCmpView' style="flex-grow:1;">
                <code class="html" style='text-align:left'>
                </code>
            </pre>
            <pre v-highlightjs='keepAliveCmpJs' style="flex-grow:1; margin-left: 10px">
                <code class="javascript" style='text-align:left'>
                </code>
            </pre>
        </div>
      </div>
    </div>
</template>
<script>
import MyKeepAliveCmp from '../components/MyKeepAliveCmp'
import Props from '../views/Props'
export default {
  name: 'MyKeepAlive',
  data () {
    return {
      selectedComponent: 'MyKeepAliveCmp',
      keepAliveViewTemplate: ` // keepAlive view template
<template>
    <div>
        <button @click="selectedComponent = 'MyKeepAliveCmp'">KeepAliveComponent</button>
        <button @click="selectedComponent = 'Props'">PropsComponent</button>
        <br /><br /><br />
        <div>Keep-Alive Component</div>
        <keep-alive>
          <component :is="selectedComponent"></component>
        </keep-alive>
        <br /><br /><br />
        <div>Normal Component</div>
        <component :is="selectedComponent"></component>
      </div>
    </div>
</template>`,
      keepAliveCmpView: ` // keepAlive component template
<template>
    <div>
        <button @click="count++">Increase</button>
        <div>{{ count }}</div>
    </div>
</template>`,
      keepAliveViewJs: ` // keepAlive view script
export default {
  name: 'MyKeepAlive',
  data () {
    return {
      selectedComponent: 'MyKeepAliveCmp'
    }
  },
  components: {
    MyKeepAliveCmp,
    Props
  }
}`,
      keepAliveCmpJs: ` // keepAlive component script
export default {
  name: 'MyKeepAliveCmp',
  data () {
    return {
      count: 0
    }
  }
}`
    }
  },
  components: {
    MyKeepAliveCmp,
    Props
  }
}
</script>




